@import "//cdn.jsdelivr.net/npm/font-kopub@1.0/kopubbatang.min.css";

.App {
  text-align: center;
  font-family: 'Nanum Gothic';
}

.modal {
  background: #fff;
  background: linear-gradient();
  border-radius: 15px;
  padding: 20px;
  max-width: 600px;
  margin: 20px auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}


.modal h1 {
  color: #000;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  font-size: 28px;
}

.modal .content {
  font-size: 18px;
  font-weight: bold;
}

.modal .button {
  color: #fff;
  border: none;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 20px 0;
  cursor: pointer;
  background-color: #000;
  border-radius: 50px;
}

.modal .button:hover {
  background-color: #2b2a2a;
}

.modal-backdrop {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}

.modal-backdrop p+p {
  color: red;
  font-weight: bold;
  font-size: 20px;
}



@media (max--width:760px) {
  .modal {
    max-width: 90%;
  }
}